




import {Component, Vue, Watch} from "vue-property-decorator"
import {Loader} from "@googlemaps/js-api-loader"
import "leaflet/dist/leaflet.css"
import GeoPosition from "@/types/geoPosition"
import {} from 'google.maps';
import locationHelpers from "@/plugins/locationHelpers"
import IAddress from "@/types/address"

const loader = new Loader({
    apiKey: process.env.VUE_APP_GOOGLE_MAPS_API_KEY,
    version: "weekly",
})

@Component
export default class AppMap extends Vue{
    map!: any
    defaultMarker!: any
    defaultInfo!: any

    pickupMarker!: any
    pickupInfo!: any

    dropoffMarker!: any
    dropoffInfo!: any

    path!: any 

    get dropoff(){
        return this.$store.state.dropoff
    }

    get pickup(){
        return this.$store.state.pickup
    }

    @Watch("pickup")
    async setPickupMarker(pickup: IAddress){
        this.closeDefaultMarkers()
        if(!pickup.lat || !pickup.lng){
            pickup = await locationHelpers.geocode(pickup)
        }
        this.map.setCenter({lat: pickup.lat, lng: pickup.lng})
        
        this.pickupMarker?.setMap(null)
        this.pickupInfo?.close()

        this.pickupMarker = new window.google.maps.Marker({
            position: {lat: pickup.lat, lng: pickup.lng},
            map: this.map,
            label: "P",
            title: "Pickup",
        })
        this.pickupInfo = new window.google.maps.InfoWindow({
            content: "<strong>Pickup</strong>",
        });
        this.pickupInfo.open(this.map, this.pickupMarker)
        this.drawPath()
    }

    @Watch("dropoff")
    async setDropoffMarker(dropoff: IAddress){
        this.closeDefaultMarkers()
        if(!dropoff.lat || !dropoff.lng){
            dropoff = await locationHelpers.geocode(dropoff)
        }
        
        this.dropoffMarker?.setMap(null)
        this.dropoffInfo?.close()

        this.dropoffMarker = new window.google.maps.Marker({
            position: {lat: dropoff.lat, lng: dropoff.lng},
            map: this.map,
            label: "D",
            title: "Dropoff",
        })
        this.dropoffInfo = new window.google.maps.InfoWindow({
            content: "<strong>Dropoff</strong>",
        });
        this.dropoffInfo.open(this.map, this.dropoffMarker)
        this.drawPath()
    }

    drawPath(){
        if(!this.pickupMarker || !this.dropoffMarker) return
        this.path?.setMap(null)
        const paths = [
            {lat: this.dropoff.lat, lng: this.dropoff.lng},
            {lat: this.pickup.lat, lng: this.pickup.lng}
        ]
        this.path = new window.google.maps.Polyline({
            path: paths,
            geodesic: true,
            strokeColor: "#FF0000",
            strokeOpacity: 1.0,
            strokeWeight: 2,
        });
        this.path.setMap(this.map)
        this.$emit("mapready")
    }
    closeDefaultMarkers(){
        this.defaultMarker?.setMap(null)
        this.defaultInfo?.close()
    }
    loadMap(center: GeoPosition){
        loader.load().then( () => {
            this.map = new window.google.maps.Map(
                    document.getElementById("map-mount"
                ) as HTMLElement, {
                    center: center,
                    zoom: 16,
            })
            this.defaultMarker = new window.google.maps.Marker({
                position: center,
                map: this.map,
                label: "P",
                title: "Current position",
            })
            this.defaultInfo = new window.google.maps.InfoWindow({
                content: "Current position",
            })
            this.defaultInfo.open(this.map, this.defaultMarker)
        })
        
    }
    async mounted(){
        const address = await locationHelpers.currentAddress()
        this.loadMap({lat: address.lat, lng: address.lng})
    }
}
