import IAddress from "@/types/address"
import GeoPosition from "@/types/geoPosition"

const toleranceRadius = process.env.VUE_APP_TOLERANCE_RADIUS || 50000 //in metres

const apiUrl = process.env.VUE_APP_API_URL

let currentLocation: GeoPosition

const setLocation = async () =>{
    if (currentLocation) return

    const loc = await new Promise<GeoPosition>((resolve, reject) => {
        navigator.geolocation.getCurrentPosition((pos) => {
            resolve({lat: pos.coords.latitude, lng: pos.coords.longitude})
        }, e => reject(e))
    })

    currentLocation = loc
}

setLocation()

const getLocation = async () =>{
    if (!currentLocation) await setLocation()
    return currentLocation
}

export default{
    async reverseGeocode(point: GeoPosition){
        const params = new URLSearchParams({
            "lat": `${point.lat}`,
            "lng": `${point.lng}`,
        })
        const  url =  `${apiUrl}/reverse?`+params.toString()
        const response = await fetch(url).then(r => r.json())
        return response.data
    },
    async currentAddress(): Promise<IAddress>{
        const currLoc = await getLocation()

        return this.reverseGeocode(currLoc)
    },
    async suggestAddress(query: string): Promise<IAddress[]>{
        const currLoc = await getLocation()
        const params = new URLSearchParams({
            "lat": `${currLoc.lat}`,
            "lng": `${currLoc.lng}`,
            "q": query,
            "r": toleranceRadius
        })
        const  url =  `${apiUrl}/suggest?`+params.toString()
        const response = await fetch(url).then(r => r.json())
        return response.data || [] as IAddress[]
    },
    async geocode(address: IAddress): Promise<IAddress>{
        const params = new URLSearchParams({
            "place_id": address.placeId
        })
        const  url =  `${apiUrl}/geocode?`+params.toString()
        const response = await fetch(url).then(r => r.json())
        address.lng = response.data.lng
        address.lat = response.data.lat
        return address
    }
}