






































import {Component, Vue} from 'vue-property-decorator'
import AddressPicker from './address-picker.vue'
import ILocation from '@/types/location'
import IAddress from '@/types/address'
type EmitedLocation = {address: IAddress, venue: string}

@Component({
    components:{
        AddressPicker
    }
})
export default class ParcelRequest extends Vue{
    pickup = {} as IAddress
    dropoff = {} as IAddress

    pickupText = ""
    dropoffText = ""
    show = false
    venue = ""
    content = ""

    pickAddress(venue: string){
        this.show = true
        this.venue = venue

        if (venue.toLowerCase() == "dropoff")
            this.content = this.dropoff.primaryText
        else
            this.content = this.pickup.primaryText
    }

    picked({address, venue}: EmitedLocation){
        if (venue.toLowerCase() == "dropoff"){
            this.dropoff = address
            this.dropoffText = address.primaryText
            this.$store.commit("setDropoff", address)
        }
        else{
            this.pickup = address
            this.pickupText = address.primaryText
            this.$store.commit("setPickup", address)
        }
    }
}
