







































































import locationHelpers from "@/plugins/locationHelpers"
import IAddress from "@/types/address"
import {Component, Prop, PropSync, Vue, Watch} from "vue-property-decorator"

import debounce from 'lodash.debounce'

@Component
export default class AddressPicker extends Vue{
    @Prop({type: Boolean, default: false})
    show!: boolean

    @Prop({type: String, required: true})
    venue!: string

    @PropSync("show", {type: Boolean, default: false})
    dialog!: boolean

    @Prop({type: String, default: ""})
    inputContent!: string

    query = ""

    selectedItem = ""

    addressStore = [] as IAddress[]

    debouncedSuggestions!: any

    currentAddress =  {} as IAddress
    get addresses(){
        return [...this.addressStore]
    }

    @Watch("query")
    async fetchSuggestions(query: string){
        this.debouncedSuggestions(query)
    }

    async getSuggestions(query: string){
        this.addressStore = []
        if(query == "") return
        const suggestions = await locationHelpers.suggestAddress(query) 
        this.addressStore = [...suggestions]
    }
    
    @Watch("selectedItem")
    addressPicked(placeId: string){
        const selected = this.addressStore.find(a => a.placeId == placeId )
        if (!selected) return
        this.$emit(
            "picked", 
            {
                address: selected,
                venue: this.venue
            }
        )
        this.dialog = false
    }
    
    @Watch("show")
    async setDefaultAddress(status: boolean){
        this.addressStore = []
        if(this.venue.toLowerCase() == "pickup" && !this.query){
            const curr = await locationHelpers.currentAddress()
            this.currentAddress = curr
            this.addressStore = [curr]
        }
    }
    @Watch("inputContent")
    async syncDisplayText(text: string){
        this.query = text
    }

    isCurrentAddress(address: IAddress){
        return this.currentAddress?.placeId == address.placeId
    }
    created(){
        this.debouncedSuggestions = debounce(this.getSuggestions, 700)
    }
}
