import IAddress from "@/types/address";
import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    pickup: {} as IAddress,
    dropoff: {} as IAddress,
  },
  mutations: {
    setDropoff(state, payload){
      state.dropoff = payload
    },
    setPickup(state, payload){
      state.pickup = payload
    }
  },
  actions: {},
  modules: {},
});
